$path: '../../';

@import "../shared/fonts";

body {
  background: transparent;
  color: #000;
  font: .875rem/1.57142857 "Open Sans", sans-serif;
  margin: 0;
  padding: 1rem;
}

h1, h2, h3, h4, h5, h6 {
  margin: 1rem 0;
  line-height: 1.2;
}

h1 {
  font-size: 2.4em;
}

h2 {
  font-size: 2em;
}

h3 {
  font-size: 1.6em;
}

h4 {
  font-size: 1.4em;
}

h5 {
  font-size: 1.2em;
}

h6 {
  font-size: 1em;
}

p {
  margin: 1rem 0;
}


table {
  border-collapse: collapse;
  min-width: 100%;
}

th,
td {
  padding: .4rem;
  border: 1px dotted #ccc;
}

hr {
  border-color: #ccc;
  border-style: solid;
  border-width: 1px 0 0 0;
}

blockquote {
  border-left: 2px solid #ccc;
  margin-left: 1.5rem;
  padding-left: 1rem;

  .mce-content-body[dir=rtl] {
    & {
      border-right: 2px solid #ccc;
      margin-right: 1.5rem;
      padding-right: 1rem;
    }
  }
}

:first-child {
  margin-top: 0;
}

:last-child {
  margin-bottom: 0;
}